import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertController, ModalController } from '@ionic/angular';
import { EMPTY, from, Observable } from 'rxjs';
import { catchError, flatMap, switchMap, map, toArray } from 'rxjs/operators';

import { ApiClientModule, ApiRequest, TagModel, ArchiveModel, ManifestModel, TagStatus } from '../include/api-client/api-client.module';
import { AppLogService } from '../include/app-log/app-log.service';
import { DefaultAvatarPhoto, DefaultCoverPhoto } from '../include/app-settings/app-settings.module';
import { AppSettingsService, IgnoreSetting, SettingKey } from '../include/app-settings/app-settings.service';
import { Translations } from '../include/i18n/i18n.module';

import { CreateDialogPage, CssClass as CreateDialogCssClass } from '../create-dialog/create-dialog.page';
import { MediaDialogPage } from '../media-dialog/media-dialog.page';

@Component({
    selector: 'app-view',
    templateUrl: './view.page.html',
    styleUrls: ['./view.page.scss'],
})
export class ViewPage implements OnInit {

    constructor(
        private route: ActivatedRoute,
        private alert: AlertController,
        private api: ApiClientModule,
        private log: AppLogService,
        private modal: ModalController,
        private nav: Router,
        private settings: AppSettingsService,
        private translations: Translations,
    ) {
        this.translations.ready([
            Translation.Alert_Flag_Title,
            Translation.Alert_Flag_Message,
            Translation.Alert_Flagged_Title,
            Translation.Alert_Flagged_Message,
            Translation.Button_Yes,
            Translation.Button_No,
            Translation.Button_OK,
        ]).then();
    }

    tagHandle: string;
    arkHandle: string;
    title: string;
    items: ViewItem[];
    locked: boolean;

    showMessage: boolean;
    hasAuthor: boolean;
    message: string;
    author: string;
    avatar: string;
    cover: string;

    backHref: string = '/';

    ngOnInit() {
        let params = this.route.snapshot.paramMap;

        this.tagHandle = params.get('tag');
        this.arkHandle = params.get('archive');

    }

    ionViewWillEnter() {
        let ignore = this.settings.get<IgnoreSetting>(SettingKey.ignore);

        if (ignore && ignore.tag && ignore.tag.indexOf(this.tagHandle) >= 0) {
            this.nav.navigate(['/']);
        } else if (!this.tagHandle || this.tagHandle.length == 0) {
            this.nav.navigate(['/']);
        } else if (!this.arkHandle || this.arkHandle.length == 0) {
            this.loadArchives();
        } else {
            this.loadContent();
        }
    }

    async open(item: ViewItem) {
        let isMedia = /(audio|video|image)/.test(item.mime);

        if (!item.isContent) {
            this.nav.navigate([`view/${this.tagHandle}/${item.handle}`]);
        } else if (isMedia) {
            let dialog = await this.modal.create({
                cssClass: 'dialog-media',
                component: MediaDialogPage,
                componentProps: {
                    uri: this.api.url(`/ark/${this.tagHandle}/${this.arkHandle}/${item.fileName}`),
                    mime: item.mime,
                    title: item.title,
                }
            });

            await dialog.present();

        } else {
            window.open(this.api.url(`/ark/${this.tagHandle}/${this.arkHandle}/${item.fileName}`), '_blank');
        }
    }

    async create() {
        let dialog = await this.modal.create({
            cssClass: CreateDialogCssClass,
            component: CreateDialogPage,
        });

        await dialog.present();
    }

    async flag() {
        let msg = await this.alert.create({
            header: this.translations.get(Translation.Alert_Flag_Title),
            message: this.translations.get(Translation.Alert_Flag_Message),
            buttons: [
                { text: this.translations.get(Translation.Button_Yes), handler: async () => await this.removeItem() },
                { text: this.translations.get(Translation.Button_No), role: 'cancel' },
            ]
        });

        await msg.present();
    }

    private loadArchives() {
        let ignore = this.settings.get<IgnoreSetting>(SettingKey.ignore);

        this.api
            .get<TagModel>(`/tag/${this.tagHandle}`)
            .pipe(
                switchMap( res => from(res.contents.filter( x => !ignore || !ignore.ark || ignore.ark.indexOf(x.handle) === -1 )) ),
                flatMap( ark => this.api.get(`/ark/${this.tagHandle}/${ark.handle}`).pipe( catchError( () => EMPTY ) ), ark => ark ),
                map( ark => {
                    return {
                        handle: ark.handle,
                        cover: DefaultCoverPhoto,
                        isContent: false,
                        title: ark.title,
                        description: ark.description,
                    }
                }),
                toArray()
            )
            .subscribe(
                list => {
                    list.forEach( x => this.coverImage(x).subscribe(src => x.cover = src) );

                    this.items = list;
                    this.locked = false;
                }
            );
    }

    private loadContent() {
        this.api
            .get<ArchiveModel>(`/tag/${this.tagHandle}/${this.arkHandle}`)
            .subscribe(
                ark => {
                    this.title = ark.title;
                    this.message = ark.description;
                    this.showMessage = true;
                    this.cover = DefaultCoverPhoto;

                    let item = {
                        handle: ark.handle,
                        isContent: false,
                    };

                    this.coverImage(item).subscribe(src => this.cover = src);
                }
            );

        this.api
            .get<ManifestModel>(`/ark/${this.tagHandle}/${this.arkHandle}`)
            .subscribe(
                res => {
                    let list = res.manifest.items.map( i => {
                        return {
                            handle: this.arkHandle,
                            cover: DefaultCoverPhoto,
                            icon: this.iconImage(i.mime),
                            isContent: true,
                            title: i.title,
                            description: i.description,
                            fileName: i.fileName,
                            mime: i.mime,
                            default: i.default,
                        };
                    });

                    list.forEach( i => this.coverImage(i).subscribe(src => i.cover = src) );

                    this.items = list;

                    let item = this.items.find( i => i.default );

                    if (item) {
                        this.open(item);
                    }

                    if (res.author) {
                        this.author = res.author.handle;

                        if (res.author.avatar) {
                            this.avatar = `data:image/png;base64,${res.author.avatar}`;
                        } else {
                            this.avatar = DefaultAvatarPhoto;
                        }

                        this.hasAuthor = true;
                    } else {
                        this.author = "Anonymous"
                        this.avatar = DefaultAvatarPhoto;
                    }
                }
            );

        this.api
            .get<TagModel>(`/tag/${this.tagHandle}`)
            .subscribe(
                tag => {
                    this.locked = tag.locked;

                    if (tag.contents.length > 1) {
                        this.backHref = `/view/${this.tagHandle}`;
                    }
                }
            );
    }

    private async removeItem() {
        let isContent = this.arkHandle && this.arkHandle.length > 0;
        let uri = isContent ? `/ark/${this.tagHandle}/${this.arkHandle}/flag` : `/tag/${this.tagHandle}/flag`;

        // this.api
        //     .put(uri)
        //     .subscribe(
        //         async () => {
                    let ignore = this.settings.get<IgnoreSetting>(SettingKey.ignore);

                    if (!ignore) {
                        ignore = {
                            tag: [],
                            ark: [],
                        };
                    }

                    if (isContent) {
                        ignore.ark.push(this.arkHandle);
                    } else {
                        ignore.tag.push(this.tagHandle);
                    }

                    this.log.trace("ignoring", ignore);

                    this.settings.set(SettingKey.ignore, {value: ignore});

                    let msg = await this.alert.create({
                        header: this.translations.get(Translation.Alert_Flagged_Title),
                        message: this.translations.get(Translation.Alert_Flagged_Message),
                        buttons: [
                            { text: this.translations.get(Translation.Button_OK) }
                        ]
                    });

                    msg.onDidDismiss().then( () => this.nav.navigate([this.backHref]) );

                    await msg.present();
            //     }
            // );
    }

    private coverImage(item: ViewItem): Observable<string> {
        return Observable.create(o => {
            if (!item.isContent) {
                let img = new Image();
                let src = this.api.url(`/ark/${this.tagHandle}/${item.handle}/_cover`);

                item.cover = DefaultCoverPhoto;

                img.onload = () => { o.next(src); o.complete(); };
                img.onerror = () => { o.next(DefaultCoverPhoto); o.complete(); };

                img.src = src;
            } else {
                let src = '';

                if (item.mime.indexOf("image") >= 0) {
                    src = this.api.url(`/ark/${this.tagHandle}/${this.arkHandle}/${item.fileName}`);
                } else if (item.mime.indexOf("text") >= 0 || item.mime.indexOf("pdf") >= 0) {
                    src = "/assets/img/content/pen.png";
                } else if (item.mime.indexOf("audio") >= 0) {
                    src = "/assets/img/content/audio.png";
                } else if (item.mime.indexOf("video") >= 0) {
                    if (item.mime === "video/youtube") {
                        let id:string = item.fileName.split('/').pop().split('.').shift();
                        src = `http://img.youtube.com/vi/${id}/mqdefault.jpg`;
                    }
                    src = "/assets/img/content/video.png";
                }

                o.next(src);
                o.complete();
            }
        })
    }

  private iconImage(mime: string): string {
    if (mime.indexOf("image") >= 0) {
      return "/assets/img/overlay/image.png";
    } else if (mime.indexOf("audio") >= 0) {
      return "/assets/img/overlay/audio.png";
    } else if (mime.indexOf("video") >= 0) {
      return "/assets/img/overlay/video.png";
    } else if (mime.indexOf("pdf") >= 0) {
      return "/assets/img/overlay/pdf.png";
    } else {
      return "/assets/img/overlay/document.png";
    }
  }
}

interface ViewItem {
    handle: string,
    title?: string,
    description?: string,
    cover?: string,
    isContent: boolean,
    fileName?: string,
    icon?: string,
    mime?: string,
    default?: boolean,
}

const Translation = {
    Alert_Flag_Title: 'Page_View.Alert_Flag_Title',
    Alert_Flag_Message: 'Page_View.Alert_Flag_Message',
    Alert_Flagged_Title: 'Page_View.Alert_Flagged_Title',
    Alert_Flagged_Message: 'Page_View.Alert_Flagged_Message',
    Button_Yes: 'Common.Button_Yes',
    Button_No: 'Common.Button_No',
    Button_OK: 'Common.Button_OK',
}