import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';

import { ApiSettings } from '../app-settings/app-settings.module';

@NgModule({
	imports: [
		CommonModule,
	],
})
export class ApiClientModule {
	private _host: string;
	private _apiVer: string;
	private _clientKey: string;
	private _clientSecret: string;

	constructor(private http: HttpClient) {
		this._host = ApiSettings.host;
		this._apiVer = ApiSettings.apiVer;
		this._clientKey = ApiSettings.clientKey;
		this._clientSecret = ApiSettings.clientSecret;
	}

	authenticate(): Observable<ApiHeader[]> {
		return Observable.create(o => {
			this.post<Authentication>(
				new ApiRequest("/auth"),
				{ key: this._clientKey, secret: this._clientSecret }
			)
			.subscribe(
				data => o.next([{ name: "Authorization", value: data.token } ]),
				err => o.error(err),
				() => o.complete()
			)
		});
	}

	get<T>(req:ApiRequest | string): Observable<T> {
		return Observable.create(o => {
			let uri = '';
			let headers: HttpHeaders = undefined;

			if (typeof req === 'string') {
				uri = this._uri(String(req));
			} else {
				let r = req as ApiRequest;

				uri = this._uri(r.path);
				headers = this._headers(r.headers);
			}

			this.http
				.get<ApiResponse<T>>(uri, {headers: headers})
				.subscribe(
					(resp) => {
						if (!resp || !resp.response) {
							o.error(new Error("Null response"));
						} else if (resp.response.status >= 300) {
							o.error(resp.response);
						} else {
							o.next(resp.data);
						}
					},
					err => {
						if (err.error && err.error.response) {
							o.error(err.error.response)
						} else {
							o.error(err)
						}
					},
					() => o.complete()
				);
		});
	}

	post<T>(req: ApiRequest | string, body?: any): Observable<T> {
		return Observable.create(o => {
			let uri = '';
			let headers: HttpHeaders = undefined;

			if (typeof req === 'string') {
				uri = this._uri(String(req));
			} else {
				let r = req as ApiRequest;

				uri = this._uri(r.path);
				headers = this._headers(r.headers);
			}

			this.http
				.post<ApiResponse<T>>(uri, body, {headers: headers})
				.subscribe(
					(resp) => {
						if (!resp || !resp.response) {
							o.error(new Error("Null response"));
						} else if (resp.response.status >= 300) {
							o.error(resp.response.statusMessage);
						} else {
							o.next(resp.data);
						}
					},
					err => {
						if (err.error && err.error.response) {
							o.error(err.error.response)
						} else {
							o.error(err)
						}
					},
					() => o.complete()
				);
		});
	}

	postFile<T>(req:ApiRequest, body?:any): Observable<T> {
		return Observable.create(o => {
			let form: FormData = new FormData();

			for (let prop in body) {
				form.append(prop, body[prop]);
			}

			this.http.post<ApiResponse<T>>(this._uri(req.path), form, {headers: this._headers(req.headers)})
				.subscribe(
					(resp) => {
						if (!resp || !resp.response) {
							o.error(new Error("Null response"));
						} else if (resp.response.status >= 300) {
							o.error(resp.response.statusMessage);
						} else {
							o.next(resp.data);
						}
					},
					err => {
						if (err.error && err.error.response) {
							o.error(err.error.response)
						} else {
							o.error(err)
						}
					},
					() => o.complete()
				);
		});
	}

	put<T>(req:ApiRequest | string, body?:any): Observable<T> {
		return Observable.create(o => {
			let uri = '';
			let headers: HttpHeaders = undefined;

			if (typeof req === 'string') {
				uri = this._uri(String(req));
			} else {
				let r = req as ApiRequest;

				uri = this._uri(r.path);
				headers = this._headers(r.headers);
			}

			this.http
				.put<ApiResponse<T>>(uri, body, {headers: headers})
				.subscribe(
					(resp) => {
						if (!resp || !resp.response) {
							o.error(new Error("Null response"));
						} else if (resp.response.status >= 300) {
							o.error(resp.response.statusMessage);
						} else {
							o.next(resp.data);
						}
					},
					err => {
						if (err.error && err.error.response) {
							o.error(err.error.response)
						} else {
							o.error(err)
						}
					},
					() => o.complete()
				);
		});
	}

	delete<T>(req:ApiRequest): Observable<T> {
		return Observable.create(o => {
			this.http
				.delete<ApiResponse<T>>(this._uri(req.path), {headers: this._headers(req.headers)})
				.subscribe(
					(resp) => {
						if (!resp || !resp.response) {
							o.error(new Error("Null response"));
						} else if (resp.response.status >= 300) {
							o.error(resp.response.statusMessage);
						} else {
							o.next(resp.data);
						}
					},
					err => {
						if (err.error && err.error.response) {
							o.error(err.error.response)
						} else {
							o.error(err)
						}
					},
					() => o.complete()
				);
		});
	}

	fileUrl(handle: string): string {
		return this._uri(`/file/${handle}`);
	}
	
	url(path: string): string {
		return this._uri(path);
	}

	private _uri(path: string): string {
		return `${this._host}/bff/${this._apiVer}${path}`;
	}

	private _headers(headers: ApiHeader[]): HttpHeaders {
		if (typeof headers === "undefined") {
			return null;
		}

		let httpHeaders: HttpHeaders = new HttpHeaders();

		headers.forEach( header => {
			httpHeaders = httpHeaders.set(header.name, header.value);
		});

		return httpHeaders;
	}
}

interface ApiStat {
	status: number,
	statusMessage: string,
	responseTime: number
}

interface ApiResponse<T>  {
	(arg: T) : T;

	response: ApiStat
	data: T
}

interface Authentication {
	token: string
}

export interface ApiHeader {
	name: string,
	value: string
}

export class ApiRequest {
	path: string;
	headers: ApiHeader[];

	constructor(path: string) {
		this.path = path;
		this.headers = [];
	}

	addHeaders(headers: ApiHeader[]) {
		if (headers) {
			headers.forEach(x => this.headers.push(x));
		}

		return this;
	}
}

export const TagStatus = {
	Active: "active",
	Review: "review",
	Suspended: "suspended",
	Archived: "archived",
	Unassigned: "unassigned",
	Hold: "hold",
}

export interface TagModel {
	handle: string,
	title: string,
	description: string,
	status: string,
	spaceAvailable: number,
	locked: boolean,
	features: string[],
	contents: ArchiveModel[],
}

export interface ArchiveModel {
	handle: string,
	title: string,
	description: string,
}

export interface ManifestModel {
    manifest: {
        items: ManifestItemModel[],
    }
	author: ManifestAuthorModel,
}

export interface ManifestItemModel {
	fileName: string,
	title: string,
	description: string,
	mime: string,
	size: number,
	default?: boolean,
}

export interface ManifestAuthorModel {
    handle: string,
    avatar: string,
    pkey: string,
}
